import { style, styleVariants } from "@vanilla-extract/css";
import { recipe } from "@vanilla-extract/recipes";
import { colorTheme, theme } from "../../../styles/theme.css";
import { sizeRelative } from "../../../utils/etc";

import { rounded } from "../button/button.css"

const shadowTemplate = {
    default: `0px 10px 10px `,
    huge: `0px 15px 20px `
}

export const wrap = style({
    display: 'flex',
    flex: 1,
    alignItems: 'center',
    gap: theme.layout.small,
    maxWidth: theme.layout.largelement,
})


export const size = styleVariants({
    small: {
        padding: `${theme.layout.tiny} ${theme.layout.small}`,
        maxWidth: theme.layout.smallElement,
    },
    medium: {
        padding: `${theme.layout.small} ${theme.layout.medium}`,
        maxWidth: theme.layout.mediumElement,
    },
    large: {
        padding: `${theme.layout.large} ${theme.layout.huge}`,
        maxWidth: theme.layout.largelement,
    }
})

export const error = styleVariants({
    true: {
        boxShadow: `${shadowTemplate.default} ${colorTheme.error.default}`,
        selectors: {
            "&:focus": {
                boxShadow: `${shadowTemplate.huge} ${colorTheme.error.light}`,
            }
        }
    },
    false: {}
})

export const variant = styleVariants({
    filled: {
        backgroundColor: colorTheme.text.body,
        color: colorTheme.background.default,
        border: "none"
    },
    outlined: {
        backgroundColor: colorTheme.background.default,
        color: colorTheme.text.body,
        borderWidth: 1,
        borderStyle: "solid"
    },
    underlined: {},
    clear: {
        backgroundColor: colorTheme.background.default,
        color: colorTheme.text.body,
        border: "none",
    }
})


export const input = recipe({
    base: {
        position: "relative",
        transition: "0.15s",
        flex: 1,
        outline: "transparent",
        fontSize: sizeRelative(16),
        appearance: "none",
        lineHeight: sizeRelative(24),
        selectors: {
            "&:focus": {
                boxShadow: theme.shadow.medium
            }
        }
    },

    variants: {
        variant,
        size,
        error,
        rounded
    },

    defaultVariants: {
        variant: "outlined",
        rounded: "medium",
        size: "medium"
    }
})


export const search = style({
    padding: theme.layout.tiny,
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
})


export const fullWidth = style({
    flex: 1,
    maxWidth: "90vw"
})