import { globalStyle, style, keyframes } from "@vanilla-extract/css";
import { colorTheme, theme } from "../../../styles/theme.css";


const fadeIn = keyframes({
    '0%': {
        opacity: 0,
        visibility: "hidden",
        pointerEvents: "none"
    },
    '100%': {
        opacity: 1,
        visibility: "visible",
        pointerEvents: "all",
    }
})

export const dialogWrap = style({
    position: "fixed",
    zIndex: 999,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    width: "100vw",
    height: "100vh",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: colorTheme.background.transparent,
    backdropFilter: theme.blur.medium,
})

export const dialog = style({
    maxWidth: theme.layout.largelement,
    maxHeight: theme.layout.hugeElement,
    overflowY: "auto",
    boxShadow: theme.shadow.large,
})

export const actions = style({
    marginTop: theme.layout.medium,
    display: "flex",
    gap: theme.layout.small,
    flexWrap: "wrap",
})

globalStyle(`${dialogWrap}.entering`, {
    animationName: fadeIn,
    animationDuration: "0.13s",
    animationTimingFunction: "ease-in"
})

// globalStyle(`${dialogWrap}.entered ${dialog}`, {
// })

globalStyle(`${dialogWrap}.exiting`, {
    animationName: fadeIn,
    animationDuration: "0.13s",
    animationTimingFunction: "ease-in",
    animationDirection: "reverse"
})

