import { memo, useState, VFC } from 'react'
import { Chat, MoreVert } from '@mui/icons-material'
import { signOut } from 'firebase/auth'
import { auth } from '../../utils/firebase'
import Avatar from '../library/avatar'
import Button from '../library/button'
import IconButton from '../library/button/icon-button'
import Dialog from '../library/Dialog'
import Input from '../library/input'
import * as styles from "./sidebar-header.css"

const SidebarHeader: VFC = () => {

    const logOut = () => signOut(auth)

    const [addEmail, setAddEmail] = useState('')
    const [dialogOpen, setDialogOpen] = useState(false)
    const [search, setSearch] = useState('')

    return <>
        <div className={styles.header} >

            <div className={styles.headerTools} >

                <Avatar onClick={logOut} />

                <div className={styles.headerActions}>
                    <IconButton
                        size={"small"}
                        caption={'chat'}
                        variant={"clear"}
                        onClick={() => console.log('Click')}
                        className={"mr-2"}
                    >
                        <Chat />
                    </IconButton>

                    <IconButton
                        size={"small"}
                        caption={'more actions'}
                        variant={"clear"}
                        onClick={() => console.log('Click')}
                    >
                        <MoreVert />
                    </IconButton>
                </div>
                
            </div>

            <div className={styles.search}>
                <Input
                    type={"search"}
                    variant={"clear"}
                    label={"Search chats here"}
                    onChange={e => setSearch(e.target.value)}
                    value={search}
                />
            </div>

            <Button
                size={"medium"}
                onClick={() => setDialogOpen(true)}
                rounded={"none"}
                variant={"clear"}
            >
                Start new conversation
            </Button>

        </div>


        <Dialog onClose={() => setDialogOpen(false)} open={dialogOpen} >
            <h3 className={"text-xl mb-2"} >Input user&apos;s email</h3>
            <Input
                label={"Your friend's email"}
                onChange={(e) => setAddEmail(e.target.value)}
                value={addEmail}
            />
        </Dialog>

    </>
}

export default memo(SidebarHeader)