import { FormEvent, useEffect, useMemo, useState, VFC } from "react"
import { useNavigate } from "react-router"
import { useSignInWithEmailAndPassword } from "react-firebase-hooks/auth"
import Button from "../../components/library/button"
import Card from "../../components/library/card"
import Input from "../../components/library/input"
import Linked from "../../components/library/link"
import { auth } from "../../utils/firebase"
import * as styles from "./login.css"

const Login: VFC = () => {

    const navigate = useNavigate()
    const [signInWithEmailAndPassword, user, loading, error] = useSignInWithEmailAndPassword(auth)

    const [email, setEmail] = useState('')
    const emailError = useMemo(() => error ? "Cant sign in" : "", [error])
    const [password, setPassword] = useState('')

    const onSubmit = (e: FormEvent) => {
        console.log("submit form");
        
        e.preventDefault()
        if (email && password) {
            signInWithEmailAndPassword(email, password)
        }
    }

    useEffect(() => {
        if (user) {
            navigate('/myMessanger')
        }
        return () => {
            setEmail('')
            setPassword('')
        }
    }, [user, navigate])

    return <div className={styles.wrap} >
        <Card>
            <h1>Log in to your account</h1>
            <form onSubmit={onSubmit} className={styles.form}>
                <Input
                    label={"Your login or email"}
                    onChange={e => setEmail(e.target.value)}
                    value={email}
                    type={"email"}
                    variant={"outlined"}
                    error={emailError}
                />
                <Input
                    label={"Your password"}
                    onChange={e => setPassword(e.target.value)}
                    value={password}
                    type={"password"}
                    variant={"outlined"}
                    error={emailError}
                />
                <Button variant={"filled"} loading={loading} type={"submit"} >Sign in</Button>
                <p>Dont have an account? <Linked to={"/register"}>Register</Linked> </p>
            </form>
        </Card>
    </div>
}

export default Login