import { ChangeEventHandler, memo, useMemo, useState, VFC } from "react"
import VisibilityIcon from '@mui/icons-material/Visibility'
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff'
import SearchIcon from '@mui/icons-material/Search'
import * as styles from "./input.css"
import IconButton from "../button/icon-button"
import { rounded } from "../button/button.css"
import clsx from "clsx"

interface Props {
    className?: string,
    value: string,
    onChange: ChangeEventHandler<HTMLInputElement>,
    variant?: keyof typeof styles.variant
    type?: "search" | "text" | "password" | "email",
    size?: keyof typeof styles.size,
    disabled?: boolean,
    label: string,
    labeled?: boolean,
    rounded?: keyof typeof rounded
    error?: string,
    fullWidth?: boolean
}
const Input: VFC<Props> = ({
    onChange,
    value,
    className,
    disabled,
    label,
    labeled,
    size = "medium",
    variant = "outlined",
    type = "text",
    rounded = "medium",
    error,
    fullWidth
}) => {

    const inputName = useMemo(() => label.replaceAll(" ", "_"), [label])
    const [showPassword, setShowPassword] = useState(false)

    return <div className={clsx(styles.wrap, fullWidth && styles.fullWidth)}>

        {type === "search" && <div className={styles.search}>
            <SearchIcon />
        </div>}

        <input
            className={clsx(
                fullWidth && styles.fullWidth,
                styles.input({
                    variant,
                    rounded,
                    size,
                    error: !!error
                }),

            )}
            type={showPassword ? "text" : type}
            onChange={onChange}
            placeholder={label}
            name={inputName}
            id={inputName}
            autoComplete={"new-password"}
            value={value}
            disabled={disabled}
        />

        {type === "password" && <IconButton
            caption={"Show Password"}
            variant={"clear"}
            onClick={() => setShowPassword(prev => !prev)}
        >
            {showPassword ? <VisibilityOffIcon /> : <VisibilityIcon />}
        </IconButton>}
    </div >
}

export default memo(Input)