import { globalStyle, style } from "@vanilla-extract/css";
import { colorTheme, theme } from "./styles/theme.css";

globalStyle('*', {
  margin: 0,
  padding: 0,
  boxSizing: 'border-box',
  fontFamily: "Nunito, sans-serif",
  borderColor: colorTheme.text.body3
})

globalStyle('html', {
  scrollBehavior: "smooth"
})

globalStyle('body', {
  fontSize: theme.font.default,
  backgroundColor: colorTheme.background.default,
  color: colorTheme.text.body,
})

globalStyle('h1', {
  fontSize: theme.font.h1,
  marginTop: theme.layout.medium,
  marginBottom: theme.layout.large
})

globalStyle('h2', {
  fontSize: theme.font.h2,
  marginBottom: theme.layout.medium
})

globalStyle('h3', {
  fontSize: theme.font.h3,
  marginBottom: theme.layout.medium
})

export const application = style({
  width: "100vw",
  height: "100vh",
})