import { styleVariants, style as styleFunc } from "@vanilla-extract/css";
import { recipe } from "@vanilla-extract/recipes";
import { colorTheme, theme } from "../../../styles/theme.css";

export const variant = styleVariants({
    filled: {
        backgroundColor: colorTheme.text.body2,
        color: colorTheme.background.default,
        border: "none",
    },
    outlined: {
        borderWidth: 1,
        borderStyle: "solid",
        borderColor: colorTheme.text.body,
        color: colorTheme.text.body,
        backgroundColor: colorTheme.background.default
    },
    clear: {
        border: "none",
        color: colorTheme.text.body,
        backgroundColor: colorTheme.background.default
    }
})

export const size = styleVariants({
    small: {
        padding: theme.layout.tiny
    },
    medium: {
        padding: theme.layout.small
    },
    large: {
        padding: theme.layout.large
    }
})

export const rounded = styleVariants({
    small: {
        borderRadius: theme.radius.small
    },
    medium: {
        borderRadius: theme.radius.medium
    },
    full: {
        borderRadius: theme.radius.full
    },
    none: {
        borderRadius: 0
    }
})

export const button = recipe({
    base: {
        display: "flex",
        alignItems: "center",
        gap: 10,
        textTransform: "uppercase",
        fontSize: 16,
        transition: "0.12s",
        cursor: "pointer",
        textAlign: "center",
        fontWeight: 500,
        selectors: {
            "&:hover": {
                filter: "brightness(1.25)",
                boxShadow: theme.shadow.medium,
            },
            "&:active": {
                filter: "brightness(0.85)",
                boxShadow: "none"
            }
        }
    },

    variants: {
        variant,
        size,
        rounded
    },


    defaultVariants: {
        size: 'medium'
    }
})

export const text = styleFunc({
    flex: 1
})