import { style as styleFunc, styleVariants } from "@vanilla-extract/css"
import { recipe } from "@vanilla-extract/recipes"
import { colorTheme } from "../../../styles/theme.css"
import { sizeRelative } from "../../../utils/etc"
import { variant, rounded } from "../button/button.css"

export const size = styleVariants({
    small: {
        width: sizeRelative(35),
        height: sizeRelative(35)
    },
    medium: {
        width: sizeRelative(50),
        height: sizeRelative(50),
    },
    large: {
        width: sizeRelative(62),
        height: sizeRelative(62),
    }
})

export const avatar = recipe({
    base: {
        // position: "relative",
        backgroundColor: colorTheme.background.light,
        cursor: "pointer",
        overflow: "hidden",
        display: "flex",
        alignItems: "center",
        justifyContent: "center"
    },

    variants: {
        variant,
        size,
        rounded
    },

    defaultVariants: {
        rounded: "full",
        size: "medium",
        variant: "outlined"
    }
})

export const img = styleFunc({
    objectFit: "cover",
    width: "100%",
    height: "100%",
    objectPosition: "center"
})