import clsx from 'clsx'
import { ReactNode, VFC } from 'react'
import * as styles from "./button.css"

type b = "reset" | "button" | "submit"
interface Props {
    rounded?: keyof typeof styles.rounded
    variant?: keyof typeof styles.variant,
    size?: keyof typeof styles.size,
    children: string,
    type?: b,
    disabled?: boolean,
    loading?: boolean,
    iconStart?: ReactNode,
    iconEnd?: ReactNode,
    onClick?: () => void,
    className?: string
}
const Button: VFC<Props> = ({
    children,
    onClick,
    disabled,
    iconEnd,
    iconStart,
    type,
    loading,
    className,
    variant = "outlined",
    size = "medium",
    rounded = "medium"
}) => {
    return <button
        onClick={onClick}
        className={clsx(
            styles.button({ variant, rounded, size }),
            className
        )}
        disabled={disabled}
        type={type ?? "button"}
    >
        {iconStart}
        {loading ? "loading..." : <div className={styles.text} >{children}</div>}
        {iconEnd}
    </button>
}

export default Button