import clsx from 'clsx'
import React, { ButtonHTMLAttributes, memo, ReactNode, VFC } from 'react'
import * as styles from "./button.css"

interface Props {
    rounded?: keyof typeof styles.rounded
    variant?: keyof typeof styles.variant,
    size?: keyof typeof styles.size,
    children: ReactNode,
    caption: string,
    type?: ButtonHTMLAttributes<HTMLButtonElement>,
    onClick?: () => void,
    className?: string,
    disabled?: boolean,
    loading?: boolean
}
const IconButton: VFC<Props> = ({
    caption,
    children,
    onClick,
    className,
    type = "button",
    rounded = "small",
    size = "medium",
    variant = "outlined",
    disabled,
    loading
}) => {
    return <button
        onClick={onClick}
        className={clsx(
            styles.button({ size, rounded, variant }),
            className
        )}
        title={caption}
        disabled={disabled}
        type={"button"}  // TODO change to dynamic property
    >
        {loading ? "loading..." : children}
    </button>
}

export default memo(IconButton)