import { createTheme, createThemeContract } from "@vanilla-extract/css"
import { sizeRelative } from "../utils/etc"


export const theme = createThemeContract({
    layout: {
        tiny: null,
        small: null,
        medium: null,
        large: null,
        huge: null,
        tinyElement: null,
        smallElement: null,
        mediumElement: null,
        largelement: null,
        hugeElement: null,
    },
    shadow: {
        tiny: null,
        medium: null,
        large: null,
        huge: null,
    },
    radius: {
        small: null,
        medium: null,
        large: null,
        full: null,
    },
    blur: {
        small: null,
        medium: null,
        large: null
    },
    font: {
        h1: null,
        h2: null,
        h3: null,
        default: null,
        small: null,
        tiny: null
    }
})

export const baseTheme = createTheme(theme, {
    layout: {
        tiny: sizeRelative(8),
        small: sizeRelative(12),
        medium: sizeRelative(16),
        large: sizeRelative(24),
        huge: sizeRelative(32),
        tinyElement: sizeRelative(320),
        smallElement: sizeRelative(384),
        mediumElement: sizeRelative(448),
        largelement: sizeRelative(518),
        hugeElement: sizeRelative(672),
    },
    shadow: {
        tiny: `0 4px 3px rgb(0 0 0 / 0.07), 0 2px 2px rgb(0 0 0 / 0.06)`,
        medium: `0 10px 8px rgb(0 0 0 / 0.04), 0 4px 3px rgb(0 0 0 / 0.1)`,
        large: `0 15px 20px rgb(0 0 0 / 0.2), 0 8px 5px rgb(0 0 0 / 0.1)`,
        huge: `0 25px 25px rgb(0 0 0 / 0.15)`,
    },
    radius: {
        small: sizeRelative(4),
        medium: sizeRelative(7),
        large: sizeRelative(15),
        full: sizeRelative(999),
    },
    blur: {
        small: `blur(${sizeRelative(5)})`,
        medium: `blur(${sizeRelative(10)})`,
        large: `blur(${sizeRelative(25)})`,
    },
    font: {
        h1: sizeRelative(32),
        h2: sizeRelative(24),
        h3: sizeRelative(18),
        default: sizeRelative(16),
        small: sizeRelative(14),
        tiny: sizeRelative(12),
    }
})

