import { useEffect, useState, VFC, ReactNode } from 'react'
import { useAuthState } from "react-firebase-hooks/auth"
import { useMediaQuery } from '@mui/material'
import clsx from "clsx"
import * as indexStyles from "./index.css"
import * as styles from './App.css';
import { baseTheme, COLOR_THEME } from './styles/theme.css';
import { auth } from './utils/firebase'
import Loader from './components/library/loading'
import { Navigate } from 'react-router'
import { Routes, Route } from 'react-router-dom'
import Messager from './pages/messanger'
import Login from './pages/authorize/login'
import Register from './pages/authorize/register'

// const Login = lazy(() => import("./pages/authorize/login"))
// const Register = lazy(() => import("./pages/authorize/register"))

interface ProtectedProps {
  children: ReactNode
}
const Protected: VFC<ProtectedProps> = ({ children }) => {

  const [user, loading, error] = useAuthState(auth)


  if (!error && !loading && user) {
    return <div className={styles.app}>
      {children}
    </div>
  }

  if (!loading && !user) {
    return <Navigate to="/myMessanger/login" />
  }
  return <><Loader /></>
}


const App: VFC = () => {

  const [theme, setTheme] = useState<'LIGHT' | 'DARK'>('LIGHT')
  const isDarkMode = useMediaQuery('(prefers-color-scheme: dark)')

  useEffect(() => {
    isDarkMode ? setTheme("DARK") : setTheme("LIGHT")
  }, [isDarkMode, theme, setTheme])


  return <div className={clsx(COLOR_THEME[theme], baseTheme, indexStyles.application)}>
    <Routes>
      <Route path={"/myMessanger/login"} element={<Login />} />
      <Route path={"/myMessanger/register"} element={<Register />} />
      <Route path={"/myMessanger"}>
        <Route index element={<Protected >
          <Messager />
        </Protected>} />
      </Route>
    </Routes>
  </div >
}

export default App;
