import { memo, ReactNode, VFC } from "react";
import Button from "../button";
import Card from "../card";
import * as styles from "./dialog.css"
import { Transition, CSSTransition } from "react-transition-group"
import clsx from "clsx";

interface Props {
    children?: ReactNode | ReactNode[],
    actions?: ReactNode | ReactNode[],  // pass <Button/> component(s) here
    onClose: () => void,
    open: boolean
}
const Dialog: VFC<Props> = ({ children, actions, onClose, open }) => {

    return <Transition
        in={open}
        timeout={100}
        unmountOnExit
        mountOnEnter
    >
        {state => <div className={clsx(styles.dialogWrap, state)} >

            <Card className={styles.dialog} >
                {children}
                <div className={styles.actions} >
                    <Button
                        onClick={() => onClose()}
                    // type={"clear"}
                    >
                        Dismiss
                    </Button>

                    {actions}

                </div>
            </Card>

        </div>}


    </Transition>

}

export default (Dialog)
