import { VFC } from "react"
import { Link } from "react-router-dom"
import * as styles from "./link.css"

interface Props {
    to: string
    children: string
}
const Linked: VFC<Props> = ({ to, children }) => {
    return <Link to={to} className={styles.link} >
        {children}
    </Link>
}

export default Linked